@import 'variables';

body {
	font-family: 'Segoe UI', sans-serif;
	scroll-behavior: smooth;
}

.hero {
	height: 100vh;
	background: url('../assets/images/coding-bg.jpg') center/cover no-repeat;
	display: flex;
	align-items: center;
	color: white;
	text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.6);
}

h2 {
	margin-bottom: 20px;
	font-weight: bold;
}

.card-title {
	color: $primary;
}
